<template>
  <div v-if="visible">
    <div class="dialog-wrap">
      <div class="dialog-title">{{ title }}</div>

      <div class="dialog-content">
        <slot name="content">
          <div class="dialog-content-text">
            {{ content }}
          </div>
        </slot>
      </div>

      <div class="dialog-footer">
        <slot name="footer">
          <div class="dialog-btn-box">
            <div class="dialog-btn cancel" @click="onCancel">
              {{ cancelText }}
            </div>
            <div class="dialog-btn confirm" @click="onConfirm">
              {{ confirmText }}
            </div>
          </div>
        </slot>
      </div>
    </div>

    <div v-if="visible" class="mask" @click="clickMask"></div>
  </div>
</template>

<script>
  export default {
    props: {
      visible: {
        type: Boolean,
        default: false,
      },
      title: {
        type: String,
        default: '',
      },
      content: {
        type: String,
        default: '',
      },
      cancelText: {
        type: String,
        default: '取消',
      },
      confirmText: {
        type: String,
        default: '复制',
      },
    },
    methods: {
      clickMask() {
        this.$emit('update:visible', false)
      },
      onCancel() {
        this.$emit('update:visible', false)
        this.$emit('cancel')
      },
      onConfirm() {
        this.$emit('confirm')
      },
    },
  }
</script>

