import Vue from 'vue'
import VueClipboard from 'vue-clipboard2'

import App from './App.vue'
import router from './router'
import store from './store'
import './assets/scss/index.scss'
import './components'
import './styles/main.less'
import Contracts from './assets/contracts.js'
const Web3 = require('web3')
      const web3 = new Web3(window.ethereum)
// 路由权限
import './permission'
// 国际化
import i18n from './i18n'

Vue.prototype.Web3 = Web3


import { NoticeBar, Row, Col ,Stepper ,Popup,Image as VanImage,RadioGroup, Radio,Pagination, Tab, Tabs} from 'vant'

Vue.config.productionTip = false

Vue.use(VueClipboard)


Vue.use(NoticeBar)
.use(Row)
.use(Col)
.use(Stepper)
.use(Popup)
.use(VanImage)
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Pagination);
Vue.use(Tab);
Vue.use(Tabs);

Vue.prototype.$contract_cic = new web3.eth.Contract(Contracts.cic.abi, Contracts.cic.address);
Vue.prototype.$contract_game = new web3.eth.Contract(Contracts.game.abi, Contracts.game.address);


new Vue({
  router,
  store,
  i18n,
  render: (h) => h(App),
}).$mount('#app')
