<template>
  <div
    class="lang-box"
    :style="{
      backgroundImage: `url('${img}')`,
    }"
    @click="change"
  ></div>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'

  import langZh from '@/assets/images/icon/lang_zh.png'
  import langEn from '@/assets/images/icon/lang_en.png'

  export default {
    computed: {
      ...mapState(['lang']),
      img() {
        return this.lang == 'zh' ? langZh : langEn
      },
    },
    methods: {
      ...mapMutations(['SET_LANG']),
      change() {
        if (this.lang == 'zh') {
          this.SET_LANG('en')
        } else {
          this.SET_LANG('zh')
        }
        window.location.reload()
      },
    },
  }
</script>

<style lang="scss" scoped>
  .lang-box {
    width: 113px;
    height: 55px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
  }
</style>
