import Vue from 'vue'
import Vuex from 'vuex'
import { register, getUserInfo } from '@/api/user'
import { getWeb3 } from '@/utils'
import { getToken, setToken, removeToken, storage,setInfo } from '@/utils'

Vue.use(Vuex)

const getDefaultState = () => {
  return {
    token: getToken(),
    userInfo: null,
    lang: storage.get('lang') || 'zh-CN', // 语言
  }
}

export default new Vuex.Store({
  state: getDefaultState(),
  mutations: {
    RESET_STATE: (state) => {
      Object.assign(state, getDefaultState())
    },
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_USER_INFO: (state, userInfo) => {
      state.userInfo = userInfo
    },
    SET_LANG: (state, lang) => {
      state.lang = lang
      storage.set('lang', lang)
    },
   
  },
  actions: {
    register({ commit }, params) {
      return new Promise((resolve, reject) => {
        register(params)
          .then((response) => {
            const { data } = response.data
            commit('SET_TOKEN', data.user_token)
            setToken(data.user_token)
            resolve(response)
          })
          .catch((error) => {
            reject(error)
          })
      })
    },
    getUserToken({ commit }) {
      return new Promise((resolve, reject) => {
        getWeb3()
          .then((web3) => {
            web3.eth.getAccounts(function (err, result) {
              // address为当前钱包地址
              let address = result[0]
              commit('SET_TOKEN', address)
              setToken(address)
              resolve(address)
            })
          })
          .catch((err) => {
            reject(error)
          })
      })
    },
    getUserInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
        getUserInfo(state.token)
          .then((response) => {
            const { data } = response.data
            commit('SET_USER_INFO', data)
            setInfo(response.data)
            resolve(data)
          })
          .catch((error) => {
            reject(error)
          })
      })
    },

    logout({ commit }) {
      removeToken() // must remove  token  first
      resetRouter()
      commit('RESET_STATE')
    },
    // logout({ commit, state }) {
    //   return new Promise((resolve, reject) => {
    //     logout(state.token)
    //       .then(() => {
    //         removeToken() // must remove  token  first
    //         resetRouter()
    //         commit('RESET_STATE')
    //         resolve()
    //       })
    //       .catch((error) => {
    //         reject(error)
    //       })
    //   })
    // },

    resetToken({ commit }) {
      return new Promise((resolve) => {
        removeToken() // must remove  token  first
        commit('RESET_STATE')
        resolve()
      })
    },
  },
  modules: {},
})
