<template>
  <div class="user-info">
    <img @click="goTo('profile')" class="user-avatar" src="../../assets/images/avatar.png" />

    <div class="right">
      <div class="user-id">ID: {{info.data.invite_code}}</div>
      <div class="user-desc" v-if="uStar || sun || speaker">
        <fg-icon
          v-if="uStar"
          class="mr-20"
          :img="require('../../assets/images/icon/u_star.png')"
          @click.native="handleShare"
        />
        <fg-icon-text v-if="sun" class="mr-20" name="star" text="232" />
        <fg-icon
         
          v-if="speaker"
          :img="require('../../assets/images/icon/speaker.png')"
          @click.native="goTo('notice','公告')"
        />
      </div>
     
      <div v-if="team">
        <p class="user-team-text">团队人数：{{info.data.friends}}</p>
        <p class="user-team-text">PK流水：{{info.data.team_usdt_flow_display}}GTF</p>
      </div>
    </div>
    <fg-icon
          v-if="qianbao"
          class="qianbao"
          :img="require('../../assets/images/icon/qianbao.png')"
          @click.native="goTo('wallet')"
        />
 
    <fg-dialog :visible.sync="visible" title="邀请好友">
      <template v-slot:content>
        <div class="dialog-content-text">
          {{ contentText }}
        </div>
      </template>

      <template v-slot:footer>
        <div class="dialog-btn-box">
          <div class="dialog-btn cancel" @click="visible = false">取消</div>
          <div class="dialog-btn confirm" @click="onConfirm">复制</div>
        </div>
      </template>
    </fg-dialog>

    <div v-if="show" @click="show = false">
      <div class="copy-dialog">复制成功</div>
      <div class="copy-mask"></div>
    </div>
  </div>
</template>

<script>
  import FgIcon from '../fg-icon'
  import FgIconText from '../fg-icon-text'
  import FgDialog from '../fg-dialog'

  export default {
    components: {
      FgIcon,
      FgIconText,
      FgDialog,
    },
    props: {
      uStar: {
        type: Boolean,
        default: false,
      },
      sun: {
        type: Boolean,
        default: false,
      },
      speaker: {
        type: Boolean,
        default: false,
      },
      team: {
        type: Boolean,
        default: false,
      },
      qianbao:{
          type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        visible: false,
        contentText: '',
        show: false,
        info:{}
      }
    },
    created(){
      this.info=JSON.parse(localStorage.getItem('userinfo'))
      console.log(this.info)
      if(this.info==undefined||this.info==null){
       this.$router.push({ name:'register'})
     }
      this.contentText=this.info.data.invite_url
    },
    methods: {
       goTo(name,title) {
        this.$router.push({ name, params: { title }})
      },
      handleShare() {
        this.visible = true
      },
      onConfirm() {
        const vm = this
        this.$copyText(this.contentText).then(
          function (e) {
            vm.visible = false
            vm.show = true
            console.log(e)
          },
          function (e) {
            alert('Can not copy')
            console.log(e)
          }
        )
      },
    },
  }
</script>

